.outfit__container {
    border: 1px solid rgba(233,233,233,1);
    background-color: rgba(255,255,255,1);
    padding-left: 0em;
    padding-right: 0em;
    margin-left: 1.52em;
    margin-right: 0em;
    position: relative;
}

.detail__controls {
    position: absolute;
    top: 145px;
    left: 40%;
    width: 405px;
    height: 100px;
    background: url('../src/images/detail_control_center.png') center center no-repeat;
    z-index: 10;
}

.outfit__large {
    height: 443px;
    text-align: -webkit-left;
}

.outfit__large a.zoom {
    height: 443px;
    width: 610px;
    display: inline-block;
    cursor: grab;
}

.outfit__large a.zoom:active {
    cursor: grabbing;
}

.outfitgrid__small {
    position: relative;
    height: 443px;
    background: url('../src/images/test__items.jpg') -60px 60px no-repeat;
}

.dots_marker {
    position: relative;
    width: 32px;
    height: 32px;
    background: url('../src/images/item_dot.png') center center no-repeat;
    cursor: grab;
    border: none;
}

.dots_marker[data-attr="dot_marker_1"] {
    top: 90px;
    left: 10px;
}

.dots_marker[data-attr="dot_marker_2"] {
    top: 255px;
    left: 86px;
}

.dots_marker[data-attr="dot_marker_3"] {
    top: 120px;
    left: 245px;
}

.dots_marker[data-attr="dot_marker_4"] {
    top: 0px;
    left: 333px;
}

.dots_marker[data-attr="dot_marker_5"] {
    top: 74px;
    left: 433px;
}

.dots_marker[data-attr="dot_marker_6"] {
    top: 144px;
    left: 373px;
}

.dots_marker[data-attr="dot_marker_7"] {
    top: 144px;
    left: 573px;
}

.dots_marker[data-attr="dot_marker_8"] {
    top: -65px;
    left: 553px;
}

.dots_marker div.tip {
    display: none;
}
